import Vue from 'vue'
import VueRouter from 'vue-router'
import Calc from '../views/Calc.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Calc',
    component: Calc
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
